
.page{
    width: 100%;
    height: 100%;
    position: absolute;    
}

.top-tags{
    bottom: auto;
    top: 30px;
}

.tags{
    color: black;
    opacity: 0.6;
    position: absolute; 
    bottom: 0;
    left: 300px;
    font-size: 22px;
    font-family: 'Vegan Style';
}

.bottom-tag-html{
    margin-left: -20px;
}

@keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
}


.container {
    width: 100%;
    will-change: contents;
    height: 100%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
  }



.container.about-page, 
.container.contact-page, 
.container.portfolio-page, 
.container.game-page{
  
  h1 {
    font-size: 60px;
    font-family: 'Milkshake';
    color:blue;
    font-weight: 400;
    margin-top: 0;
    position: relative;
    margin-bottom: 40px;
    left: 100px;
  
    &:before {
      content: 'printf(" ");';
      font-family: 'Milkshake';
      color: red;
      font-size: 18px;
      font-weight: 400;
      position: absolute;
      margin-top: -2px;
      left: -10px;
      opacity: 0.6;
      line-height: 18px;
    }
  
    &:after {
      content: 'cout<<" ";';
      font-family: 'Milkshake';
      color: red;
      font-size: 18px;
      font-weight: 400;
      line-height: 18px;
      position: absolute;
      left: -30px;
      bottom: -20px;
      margin-left: 20px;
      margin-bottom: 20px;
      opacity: 0.6;
    }
  }
  
  p {
    margin-top: 5%;
    margin-left: 15%;
    font-size: 13px;
    color: white;
    font-family: Helvetica;
    font-weight: 700;
    max-width: fit-content;
    animation: pulse 1s;
    &:nth-of-type(1) {
      animation-delay: 1.1s;
    }
    &:nth-of-type(2) {
      animation-delay: 1.2s;
    }
    &:nth-of-type(3) {
      animation-delay: 1.3s;
    }
  }
  
  .text-zone {
    position: absolute;
    left: 10%;
    top: 50%;
    transform: translateY(-50%);
    width: 35%;
    vertical-align: middle;
    display: table-cell;
    max-height: 90%;
  }
  
  .text-animate-hover {
    &:hover {
      color: rgb(184, 4, 255);
    }
  }
}

@media screen and (max-width: 1200px) {
  .container.about-page,
  .container.contact-page,
  .container.portfolio-page {
    h1 {
      font-size: 40px;

      &:before {
        margin-top: -10px;
      }

      &:after {
        margin-bottom: 15px;
      }
    }

    p {
      font-size: 9px;
    }

    .text-zone {
      width: 40%;
    }
  }
}

